    // 		return Observable.of(this.nodeList);
    // 	} else if(this.observable) {
    // 		return this.observable;
    // 	} else {
    // 		this.http.get(this.baseUrl).subscribe(resp => { return }
    // 			.map(response => {
    // 				if(response.status == 200) {
    // 					this.nodeList = response.json() || [];
    // 					return Observable.of(this.nodeList);
    // 				}
    // 			})
    // 			.catch(this.handleError);
    // 		return this.observable;
    // 	}
