import { Component, Input } from '@angular/core';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { NgForOf } from '@angular/common';
import { NgIf } from '@angular/common';
import { BrowserModule, DomSanitizer } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';  


@Component({
  selector: 'isp-modal-content',
  templateUrl: 'isp-modal.component.html'
})
export class NgbdModalContent {
  @Input() node;
  @Input() content;

  constructor(public activeModal: NgbActiveModal, private http: Http) {}

  ngOnInit() {
  	let headers = new Headers({ 'Content-Type': 'text/html', 'Accept': 'text/html' });
		let options = new RequestOptions({ headers: headers });
  	this.http.get(this.node.legacyUrl, options).subscribe( results => {
  		console.log(results);
  		let htmlDoc = new DOMParser().parseFromString(results.text(), "text/html");
  		let frag  = htmlDoc.getElementById('block-system-main');
  		let elem = frag.getElementsByClassName('user-picture');
  		while(elem[0]) {
          elem[0].parentNode.removeChild(elem[0]);
      }

  		elem = frag.getElementsByClassName('easy_social-widget');
  		while(elem[0]) {
          elem[0].parentNode.removeChild(elem[0]);
      }
  		this.content = frag.outerHTML;
  		console.log(this.content);
  	}, error => {
  		console.log(error);
  	});
  }
}



