import { Directive, ElementRef,  HostListener  } from '@angular/core';
import { NgbdModalContent } from './isp-modal/isp-modal.component';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { NodeService } from './node-model/node.service';
import { INode } from './node-model/node';
import { Observable } from 'rxjs/Observable';


@Directive({
  selector: '[appIspSelect]',
  providers: [ NgbdModalContent, NodeService ]
})
export class IspSelectDirective {

  el: ElementRef;
  modal: NgbModal;
  node: INode;

  constructor(el: ElementRef, private modalService: NgbModal, private nodeService: NodeService ) {
        this.el = el; 
        this.modal = modalService;
        el.nativeElement.style.cursor = 'pointer';
  }

  @HostListener('click') onClick() {

  		console.log("The element ID is " + this.el.nativeElement.id);
	    this.nodeService.getNodes().subscribe((_nodes) => {
	    	for(let x=0;x<_nodes.length;x++) {
	    		if(_nodes[x].elementID === this.el.nativeElement.id) {
			    	console.log("In the click event with a node from nodeService: ");
				    console.log(_nodes[x]);
				    const modalRef = this.modal.open(NgbdModalContent, { size: 'lg' });
				    modalRef.componentInstance.node = _nodes[x];
				}
			}
	    }, () => {
	    	console.log("There was an ERROR!");
	    });
  }

}


