import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BrowserModule, DomSanitizer } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';  
import { NgModule, InjectionToken } from '@angular/core';
import { RestangularModule, Restangular } from 'ngx-restangular';

import { AppComponent } from './app.component';

// The directive that designates a clickable node
import { IspSelectDirective } from './isp-select.directive';



// The modal that shows the nodes content
import { NgbdModalContent } from './isp-modal/isp-modal.component';

// The data models
import { ILink } from './link-model/link';
import { LinkService } from './link-model/link.service';
import { INode } from './node-model/node';
import { NodeService } from './node-model/node.service';

// The SVG component
import { IspSvgComponent } from './isp-svg/isp-svg.component';
import { SafePipe } from './safe.pipe';

// Function for setting the default restangular configuration
export function RestangularConfigFactory (RestangularProvider) {
  RestangularProvider.setBaseUrl('http://localhost:8000/');
  RestangularProvider.addResponseInterceptor((data, operation, what, url, response) => {
    if(operation === "getList") {
      return data.results;
    } 
  })
}

@NgModule({
  declarations: [
    AppComponent,
    IspSelectDirective,
    NgbdModalContent,
    IspSvgComponent,
    SafePipe
  ],
  entryComponents: [ NgbdModalContent ],
  imports: [
  	RestangularModule.forRoot(RestangularConfigFactory),
    NgbModule.forRoot(),
    BrowserModule,
    CommonModule
  ],
  providers: [],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



